import React,{Component} from 'react'
import { hashHistory } from 'react-router'
import { Form, Button, message, Radio, Spin, Input, Select, Cascader, Upload, Icon } from 'antd';
const FormItem = Form.Item;
const Option = Select.Option;
const RadioGroup = Radio.Group;
const { TextArea } = Input;
import InvestItem from '../FormItem/investItem.jsx'

export class TransferForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            disabled: props.actionType == "detail" ? true : false,
            loading: false,
            "assetsType": {
                "debts":[],
                "stock": [],
                "purchase": [],
                "assetsAcquisition": []
            },
            "industryList": [],
            "cascaderRegion": [],
            "project": {   //
                "key": "", // 
                "title": "", //
                "category": "", //
                "type": "", //
                "detail": "",   //
                "accessory": {        //
                    "name": "",
                    "link": ""
                }
            },
            "financing": {  //
                "industry": "", //
                "aptitude": "", //
                "parentAptitued": "", //
                "region": [], //
                "previousProfits": "", //
                "recentProfits": "",
                "nextProfits": "",
                "number": "", //
                "deadline": "", //
                "maxRate": "" //
            },
            "assets": {
                "region": ["0","1","2"],
                "value": 214,
                "number": 2145
            },
            "mortgage": {       //
                "isNeed": "",    //
                "type": [],  //
                "name": "",  //
                "value": "",  //
                "rate": "" //
            },
            "guarantee": {
                "isNeed": "",    //
                "method": ""   //
            },
            "stock": {
                "type": "",  //:1   :2
                "property": "",  //:1   :2
                "number": "",  //
                "rate": "", //
                "estimate": {  //
                    "times": "",
                    "type": ""
                }
            },
            "target": {  //
                "name": "",
                "industry": "",
                "region": "",
                "value": "", //
                "previousProfits": "",
                "recentProfits": "",
                "nextProfits": ""
            },
            'fileList': []
        };
        ['handleSubmit', 'resetForm', 'uploadChange'].forEach(m=>this[m] = this[m].bind(this))
    }

    componentWillMount() {
        let that = this;
        this.setState({loading: true});
        adminAjax({
            url: "/delegateProject/getInfoDetail",
            method: "post",
            data: {id: that.props.infoID ? that.props.infoID : ""},
            callback: function(data) {
                let newData = data;
                that.setState(Object.assign({loading: false}, newData));
                that.setState({
                    fileList: [{
                        uid: "random",
                        name: newData.accessory.name,
                        url: newData.accessory.link,
                        status: "done",
                        response: 'Server Error 500'
                    }]
                })
            }
        })
    }

    handleSubmit(e) {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if(!err) {
                adminAjax({
                    url: "/delegateProject/edit",
                    method: "post",
                    data: this.props.actionType == "edit" ? Object.assign(values, {id: this.props.infoID}, {accessory: {name: this.state.accessory.name, link: this.state.accessory.link}}) : Object.assign(values, {accessory: {name: this.state.accessory.name, link: this.state.accessory.link}}),
                    callback: function(data) {
                        message.success("!", 1, function() {
                            hashHistory.push("/delegateProject")
                        });
                    }
                });
            }
        })
    }

    return() {
        history.back();
    }

    resetForm() {
        this.props.form.resetFields();
    }

    uploadChange(info) {
        let that = this;
        let file = info.file;
        let fileList = info.fileList;
        this.setState({loading: true});
        if(fileList.length > 0) {
            if(fileList.length > 1) {
                fileList = [file];
            }
            if(fileList[0].status == "error") {
                fileList = [];
                message.error("!");
            }
            if(fileList[0].status == "done") {
                fileList[0].url = `${fileList[0].response.url}`;
                this.setState({
                    accessory: {
                        name: fileList[0].name,
                        link: fileList[0].url
                    }
                })
            }
        } else {
            this.setState({
                accessory: {
                    name: "",
                    link: ""
                }
            })
        }

        this.setState({fileList, loading: false});
    }

    render() {
        const { getFieldDecorator } = this.props.form;
        const formItemLayout = {
            labelCol: { span: 3 },
            wrapperCol: { span: 14 }
        };
        return (
            <Spin spinning={this.state.loading} tip="Loading...">
                <Form>
                    <FormItem
                        required
                        {...formItemLayout}
                        label=""
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("project.title", {
                            rules: [{require: true, message: "!"}],
                            initialValue: this.state.project.title
                        })(
                            <Input disabled={this.state.disabled} />
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label=""
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("project.category", {
                            initialValue: "4"
                        })(
                            <Select disabled>
                                <Option value="1"></Option>
                                <Option value="2"></Option>
                                <Option value="4"></Option>
                            </Select>
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label=""
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("project.type", {
                            rules: [{
                                required: true,
                                message: "!"
                            }],
                            initialValue: this.state.project.type
                        })(
                            <Select disabled={this.state.disabled}>
                                {
                                    this.state.assetsType.assetsAcquisition.map(function(item) {
                                        return <Option value={item.key} key={item.key}>{item.value}</Option>
                                    })
                                }
                            </Select>
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label=""
                    >
                        <div></div>
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label=""
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("assets.region", {
                            rules: [{
                                required: true,
                                message: "!"
                            }],
                            initialValue: this.state.assets.region
                        })(
                            <Cascader disabled={this.state.disabled}
                                      options={this.state.cascaderRegion} />
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label=""
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("assets.number", {
                            rules: [{
                                required: true,
                                message: "!"
                            }],
                            initialValue: this.state.assets.number
                        })(
                            <Input disabled={this.state.disabled} style={{width: "93%"}} />
                        )}
                        <span className="ant-form-text" style={{marginLeft: 7}}></span>

                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label=""
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("assets.value", {
                            rules: [{
                                required: true,
                                message: "!"
                            }],
                            initialValue: this.state.assets.value
                        })(
                            <Input disabled={this.state.disabled} style={{width: "93%"}} />
                        )}
                        <span className="ant-form-text" style={{marginLeft: 7}}></span>
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label=""
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("project.detail", {
                            rules: [{require: true, message: "!"}],
                            initialValue: this.state.project.detail
                        })(
                            <TextArea disabled={this.state.disabled} autosize={{ minRows: 3 }} />
                        )}
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label=""
                    >
                        <Upload
                            name="file"
                            action="/admin/upload"
                            fileList={this.state.fileList}
                            onChange={this.uploadChange}
                        >
                            <Button>
                                <Icon type="upload" />
                            </Button>
                        </Upload>
                    </FormItem>
                    <FormItem wrapperCol={{ span: 8, offset: 3 }}>
                        {
                            !this.state.disabled ? <Button type="primary" onClick={this.handleSubmit} style={{"marginRight": "20px"}}></Button> : ""
                        }
                        {
                            !this.state.disabled ? <Button type="danger" onClick={this.resetForm} style={{"marginRight": "20px"}}></Button> : ""
                        }
                        <Button icon="rollback" onClick={this.return}></Button>
                    </FormItem>
                </Form>
            </Spin>
        )
    }
}

const WrappedTransferForm = Form.create({})(TransferForm);

module.exports = WrappedTransferForm;


