import React,{Component} from 'react'
import { hashHistory } from 'react-router'
import { Form, Button, message, Radio, Spin, Input, Select, Cascader, Checkbox, Upload, Icon } from 'antd';
const FormItem = Form.Item;
const Option = Select.Option;
const RadioGroup = Radio.Group;
const CheckboxGroup = Checkbox.Group;
const { TextArea } = Input;
export class DebtsForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            disabled: props.actionType == "detail" ? true : false,
            loading: false,
            "assetsType": {
                "debts":[],
                "stock": [],
                "purchase": [],
                "transfer": []
            },
            "industryList": [],
            "cascaderRegion": [],
            "project": {   //
                "key": "", // 
                "title": "", //
                "category": "", //
                "type": "", //
                "detail": "",   //
                "accessory": {        //
                    "name": "",
                    "link": ""
                }
            },
            "financing": {  //
                "industry": "", //
                "aptitude": "", //
                "parentAptitue": "", //
                "region": [], //
                "previousProfits": "", //
                "recentProfits": "",
                "nextProfits": "",
                "number": "", //
                "deadline": "", //
                "maxRate": "" //
            },
            "mortgage": {       //
                "isNeed": "",    //
                "type": [],  //
                "name": "",  //
                "value": "",  //
                "rate": "" //
            },
            "guarantee": {
                "isNeed": "",    //
                "method": ""   //
            },
            "stock": {
                "type": "",  //:1   :2
                "property": "",  //:1   :2
                "number": "",  //
                "rate": "", //
                "value": "", //
                "estimate": {  //
                    "times": "",
                    "type": ""
                }
            },
            "subject": {  //
                "name": "",
                "industry": "",
                "region": "",
                "previousProfits": "",
                "recentProfits": "",
                "nextProfits": ""
            },
            "assets": { //
                "region": "",
                "number": "",
                "value": ""
            },
            "audit": {
                "status": "",
                "comment": ""
            },
            'fileList': []
        };
        ['handleSubmit', 'resetForm', 'financingIndustryChange', 'mortgageChange', 'guaranteeChange', 'uploadChange'].forEach(m=>this[m] = this[m].bind(this))
    }

    guaranteeChange(e) {
        this.setState({
            guarantee: {
                isNeed: e.target.value
            }
        })
    }

    mortgageChange(e) {
        this.setState({
            mortgage: {
                isNeed: e.target.value
            }
        })
    }

    financingIndustryChange(val) {
        this.state.financing.industry = val;
        this.setState({
            financing: this.state.financing
        })
    }

    componentWillMount() {
        let that = this;
        this.setState({loading: true});
        adminAjax({
            url: "/delegateProject/getInfoDetail",
            method: "post",
            data: {id: that.props.infoID ? that.props.infoID : ""},
            callback: function(data) {
                let newData = data;
                that.setState(Object.assign({loading: false}, newData));
                that.setState({
                    fileList: [{
                        uid: "random",
                        name: newData.accessory.name,
                        url: newData.accessory.link,
                        status: "done",
                        response: 'Server Error 500'
                    }]
                })
            }
        })
    }

    handleSubmit(e) {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if(!err) {
                adminAjax({
                    url: "/delegateProject/edit",
                    method: "post",
                    data: this.props.actionType == "edit" ? Object.assign(values, {id: this.props.infoID}, {accessory: {name: this.state.accessory.name, link: this.state.accessory.link}}) : Object.assign(values, {accessory: {name: this.state.accessory.name, link: this.state.accessory.link}}),
                    callback: function(data) {
                        message.success("!", 1, function() {
                            hashHistory.push("/delegateProject")
                        });
                    }
                });
            }
        })
    }

    return() {
        history.back();
    }

    resetForm() {
        this.props.form.resetFields();
    }

    uploadChange(info) {
        let that = this;
        let file = info.file;
        let fileList = info.fileList;
        this.setState({loading: true});
        if(fileList.length > 0) {
            if(fileList.length > 1) {
                fileList = [file];
            }
            if(fileList[0].status == "error") {
                fileList = [];
                message.error("!");
            }
            if(fileList[0].status == "done") {
                fileList[0].url = `${fileList[0].response.url}`;
                this.setState({
                    accessory: {
                        name: fileList[0].name,
                        link: fileList[0].url
                    }
                })
            }
        } else {
            this.setState({
                accessory: {
                    name: "",
                    link: ""
                }
            })
        }

        this.setState({fileList, loading: false});
    }

    render() {
        const { getFieldDecorator } = this.props.form;
        const formItemLayout = {
            labelCol: { span: 3 },
            wrapperCol: { span: 14 }
        };
        return (
            <Spin spinning={this.state.loading} tip="Loading...">
                <Form>
                    <FormItem
                        required
                        {...formItemLayout}
                        label=""
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("project.title", {
                            rules: [{required: true, message: "!"}],
                            initialValue: this.state.project.title
                        })(
                            <Input disabled={this.state.disabled} />
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label=""
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("category", {
                            initialValue: "1"
                        })(
                            <Select disabled>
                                <Option value="1"></Option>
                                <Option value="2"></Option>
                                <Option value="4"></Option>
                            </Select>
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label=""
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("project.type", {
                            rules: [{
                                required: true,
                                message: "!"
                            }],
                            initialValue: this.state.project.type
                        })(
                            <Select disabled={this.state.disabled}>
                                {
                                    this.state.assetsType.debts.map(function(item) {
                                        return <Option value={item.key} key={item.key}>{item.value}</Option>
                                    })
                                }
                            </Select>
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label=""
                    >
                        <div></div>
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label=""
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("industry", {
                            rules: [{
                                required: true,
                                message: "!"
                            }],
                            initialValue: this.state.financing.industry
                        })(
                            <Select onChange={this.financingIndustryChange} disabled={this.state.disabled}>
                                {
                                    this.state.industryList.map(function(item) {
                                        return <Option value={item.key} key={item.key}>{item.value}</Option>
                                    })
                                }
                            </Select>
                        )}
                    </FormItem>
                    {
                        this.state.financing.industry == 2 ?
                            <FormItem
                                required
                                {...formItemLayout}
                                label=""
                                hasFeedback={!this.state.disabled}
                            >
                                {getFieldDecorator("financing.aptitude", {
                                    rules: [{
                                        required: true,
                                        message: "!"
                                    }],
                                    initialValue: this.state.financing.aptitude
                                })(
                                    <Input disabled={this.state.disabled} />
                                )}
                            </FormItem> : ""
                    }
                    {
                        this.state.financing.industry == 2 ?
                            <FormItem
                                required
                                {...formItemLayout}
                                label=""
                                hasFeedback={!this.state.disabled}
                            >
                                {getFieldDecorator("financing.parentAptitue", {
                                    rules: [{
                                        required: true,
                                        message: "!"
                                    }],
                                    initialValue: this.state.financing.parentAptitue
                                })(
                                    <Input disabled={this.state.disabled} />
                                )}
                            </FormItem> : ""
                    }
                    <FormItem
                        required
                        {...formItemLayout}
                        label=""
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("financing.region", {
                            rules: [{
                                required: true,
                                message: "!"
                            }],
                            initialValue: this.state.financing.region
                        })(
                            <Cascader disabled={this.state.disabled} options={this.state.cascaderRegion} />
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label=""
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("financing.previousProfits", {
                            rules: [{
                                required: true,
                                message: "!"
                            }],
                            initialValue: this.state.financing.previousProfits
                        })(
                            <Input style={{width: "93%"}} disabled={this.state.disabled} />
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label=""
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("financing.number", {
                            rules: [{
                                required: true,
                                message: "!"
                            }],
                            initialValue: this.state.financing.number
                        })(
                            <Input style={{width: "93%"}} disabled={this.state.disabled} />
                        )}
                        <span className="ant-form-text" style={{marginLeft: 7}}></span>

                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label=""
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("financing.deadline", {
                            rules: [{
                                required: true,
                                message: ""
                            }],
                            initialValue: this.state.financing.deadline
                        })(
                            <Input style={{width: "93%"}} disabled={this.state.disabled} />
                        )}
                        <span className="ant-form-text" style={{marginLeft: 7}}></span>
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label=""
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("financing.maxRate", {
                            rules: [{
                                required: true,
                                message: ""
                            }],
                            initialValue: this.state.financing.maxRate
                        })(
                            <Input style={{width: "93%"}} disabled={this.state.disabled} />
                        )}
                        <span className="ant-form-text" style={{marginLeft: 7}}>%</span>

                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="/"
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("mortgage.isNeed", {
                            rules: [{
                                required: true,
                                message: "!"
                            }],
                            initialValue: this.state.mortgage.isNeed
                        })(
                            <RadioGroup onChange={this.mortgageChange} disabled={this.state.disabled}>
                                <Radio value="1"></Radio>
                                <Radio value="0"></Radio>
                            </RadioGroup>
                        )}
                    </FormItem>
                    {
                        this.state.mortgage.isNeed == "1" ?
                            <FormItem
                                {...formItemLayout}
                                label="/"
                            >
                                {getFieldDecorator("mortgage.type", {
                                    initialValue: this.state.mortgage.type
                                })(
                                    <CheckboxGroup disabled={this.state.disabled}>
                                        <Checkbox value="1"></Checkbox>
                                        <Checkbox value="2"></Checkbox>
                                        <Checkbox value="3"></Checkbox>
                                    </CheckboxGroup>
                                )}
                            </FormItem> : ""
                    }
                    {
                        this.state.mortgage.isNeed == "1" ?
                            <FormItem
                                {...formItemLayout}
                                label="/"
                            >
                                {getFieldDecorator("mortgage.name", {
                                    initialValue: this.state.mortgage.name
                                })(
                                    <Input disabled={this.state.disabled} />
                                )}
                            </FormItem> : ""
                    }
                    {
                        this.state.mortgage.isNeed == "1" ?
                            <FormItem
                                {...formItemLayout}
                                label="/"
                            >
                                {getFieldDecorator("mortgage.value", {
                                    initialValue: this.state.mortgage.value
                                })(
                                    <Input style={{width: "93%"}} disabled={this.state.disabled} />
                                )}
                                <span className="ant-form-text" style={{marginLeft: 7}}></span>
                            </FormItem> : ""
                    }
                    {
                        this.state.mortgage.isNeed == "1" ?
                            <FormItem
                                {...formItemLayout}
                                label="/"
                            >
                                {getFieldDecorator("mortgage.rate", {
                                    initialValue: this.state.mortgage.rate
                                })(
                                    <Input style={{width: "95%"}} disabled={this.state.disabled} />
                                )}
                                <span className="ant-form-text" style={{marginLeft: 7}}>%</span>
                            </FormItem> : ""
                    }
                    <FormItem
                        required
                        {...formItemLayout}
                        label=""
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("guarantee.isNeed", {
                            rules: [{
                                required: true,
                                message: "!"
                            }],
                            initialValue: this.state.guarantee.isNeed
                        })(
                            <RadioGroup onChange={this.guaranteeChange} disabled={this.state.disabled}>
                                <Radio value="1"></Radio>
                                <Radio value="0"></Radio>
                            </RadioGroup>
                        )}
                    </FormItem>
                    {
                        this.state.guarantee.isNeed == "1" ?
                            <FormItem
                                {...formItemLayout}
                                label=""
                            >
                                {getFieldDecorator("guarantee.method", {
                                    initialValue: this.state.guarantee.method
                                })(
                                    <Input disabled={this.state.disabled} />
                                )}
                            </FormItem> : ""
                    }
                    <FormItem
                        required
                        {...formItemLayout}
                        label=""
                        hasFeedback={!this.state.disabled}
                    >
                        {getFieldDecorator("project.detail", {
                            rules: [{require: true, message: "!"}],
                            initialValue: this.state.project.detail
                        })(
                            <TextArea disabled={this.state.disabled} autosize={{ minRows: 3 }} />
                        )}
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label=""
                    >
                        <Upload
                            name="file"
                            action="/admin/upload"
                            fileList={this.state.fileList}
                            onChange={this.uploadChange}
                        >
                            <Button>
                                <Icon type="upload" />
                            </Button>
                        </Upload>
                    </FormItem>
                    <FormItem wrapperCol={{ span: 8, offset: 3 }}>
                        {
                            !this.state.disabled ? <Button type="primary" onClick={this.handleSubmit} style={{"marginRight": "20px"}}></Button> : ""
                        }
                        {
                            !this.state.disabled ? <Button type="danger" onClick={this.resetForm} style={{"marginRight": "20px"}}></Button> : ""
                        }
                        <Button icon="rollback" onClick={this.return}></Button>
                    </FormItem>
                </Form>
            </Spin>
        )
    }
}

const WrappedDebtsForm = Form.create({})(DebtsForm);

module.exports = WrappedDebtsForm;


